<template>
  <div class="loginBox">
    <div class="title">登录</div>
    <text-input :obj="user" :attr="'sno'" :defualt="'学号'"></text-input>
    <password-input :obj="user" :attr="'pwd'" :default="'密码'" :events="pwdEvents"></password-input>
    <button @click="submit">确定</button>
    <a @click="$router.push('/login/register')">没有账号？立即注册</a>
  </div>
</template>

<script>
import PasswordInput from './PasswordInput.vue';
import TextInput from './TextInput.vue';
import { login } from 'network/Login';

export default {
  name: 'LoginBox',
  data () {
    return {
      user: {
        sno: '',
        username: '',
        pwd: ''
      },
    };
  },
  methods: {
    submit () {
      let sno = this.user.sno;
      let pwd = this.user.pwd;
      login({
        sno,
        pwd
      }).then(data => {
        if (data.data.status) {
          this.$store.commit('setSno', sno);
          tool.setCookie({
            sno
          }, 1);
          this.$router.push('/home');
        } else {
          alert('用户名或密码错误');
        }
      });
    },
  },
  computed: {
    pwdEvents () {
      return [{
        type: 'keydown',
        fun: (e) => {
          if (e.key == 'Enter') {
            this.submit();
          }
        },
      }]
    }
  },
  components: {
    TextInput,
    PasswordInput
  },

}
</script>

<style>
.loginBox {
  margin: 160px auto;
  width: 360px;
  text-align: center;
}
</style>


